.header {
  color: var(--black-second-color);
  transition: background-color var(--time) var(--cubik);
  border: 1px solid #000;
  align-content: center;
  padding: 28px 0 0;
}

.header-container {
  border: 1px solid red;
  align-items: center;
  display: flex;
}

.navigation {
  display: none;
}

.nav-item {
  align-items: center;
  display: flex;
}

.nav-item-link {
  color: var(--black-second-color);
  transition: color var(--time) var(--cubik);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.nav-item-link:hover, .nav-item-link:focus {
  color: var(--breen-color);
}

.nav-item:not(:last-child) {
  margin-right: 16px;
}

.logo {
  letter-spacing: -.64px;
  color: var(--black-second-color);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.logo-link {
  margin-right: auto;
}

.logo-green {
  color: var(--breen-color);
}

.basket-link {
  align-items: center;
  margin-right: 14px;
  display: flex;
}

.basket-icon {
  fill: none;
  stroke: var(--black-second-color);
  transition: stroke var(--time) var(--cubik);
}

.basket-icon:hover, .basket-icon:focus {
  stroke: var(--breen-color);
}

.menu-toggle-icon {
  stroke: var(--black-second-color);
  transition: stroke var(--time) var(--cubik);
}

.menu-toggle-icon:hover, .menu-toggle-icon:focus {
  stroke: var(--breen-color);
}

.menu-toggle-open {
  align-items: center;
  display: flex;
}

.check-text {
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (max-width: 767px) {
  .check-text {
    display: none;
  }
}

.check-text:before {
  content: "";
  width: 46px;
  height: 20px;
  transition: background-color var(--time) var(--cubik);
  background-color: #cecdcd;
  border-radius: 50px;
  justify-content: center;
  display: flex;
  box-shadow: 7px 5px 15px rgba(160, 158, 160, .6);
}

.check-text:after {
  content: "";
  width: 18px;
  height: 18px;
  background-color: var(--white-second-color);
  transition: background-color var(--time) var(--cubik), transform var(--time) var(--cubik);
  border-radius: 50%;
  position: absolute;
  left: 2px;
}

.check:checked + .check-text:after {
  background-color: var(--whiit-first-color);
  transform: translateX(24px);
}

.check:checked + .check-text:before {
  background-color: var(--breen-color);
}

.header.light-theme {
  background-color: var(--whiit-first-color);
}

.header.dark-theme {
  background-color: var(--black-second-color);
}

@media screen and (min-width: 768px) {
  .navigation {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .menu-button-container {
    display: none;
  }

  .logo-link {
    margin: 0 auto;
  }

  .logo {
    font-size: 18px;
  }

  .basket-link {
    margin-right: 18px;
  }
}

